/* Dashboard Container */
.dashboard-container {
  max-width: 1400px;
  margin: 0 auto;
  padding: var(--space-6) var(--space-4);
  animation: fadeInUp var(--transition-slow) ease-out;
}

/* Dashboard Grid */
.dashboard-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: var(--space-6);
  margin: var(--space-6) 0;
}

/* Stat Card */
.stat-card {
  background: var(--glass-bg);
  border-radius: var(--radius-2xl);
  padding: var(--space-6);
  border: 1px solid var(--glass-border);
  backdrop-filter: blur(16px);
  -webkit-backdrop-filter: blur(16px);
  position: relative;
  overflow: hidden;
  transition: var(--transition-normal);
  animation: cardAppear 0.6s cubic-bezier(0.4, 0, 0.2, 1) forwards;
  transform: translateY(20px);
  opacity: 0;
  box-shadow: var(--shadow-sm);
}

.stat-card::before {
  content: '';
  position: absolute;
  inset: 0;
  background: linear-gradient(
    125deg,
    transparent 0%,
    rgba(108, 92, 231, 0.05) 30%,
    rgba(0, 210, 211, 0.05) 70%,
    transparent 100%
  );
  opacity: 0;
  transition: opacity var(--transition-normal);
}

.stat-card:hover {
  transform: translateY(-5px);
  box-shadow: var(--shadow-lg);
  border-color: var(--border-medium);
}

.stat-card:hover::before {
  opacity: 1;
}

.stat-icon {
  width: 48px;
  height: 48px;
  border-radius: var(--radius-lg);
  background: var(--gradient-primary);
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: var(--space-4);
  font-size: var(--font-size-xl);
  color: var(--text-primary);
  transition: var(--transition-fast);
}

.stat-card:hover .stat-icon {
  transform: scale(1.05);
}

.stat-title {
  font-size: var(--font-size-sm);
  color: var(--text-secondary);
  text-transform: uppercase;
  letter-spacing: 1px;
  margin-bottom: var(--space-2);
  font-weight: var(--font-weight-medium);
}

.stat-value {
  font-size: var(--font-size-3xl);
  font-weight: var(--font-weight-bold);
  color: var(--text-primary);
  margin-bottom: var(--space-2);
  background: var(--gradient-primary);
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
  background-size: 200% 200%;
  animation: gradientShift 8s ease infinite;
}

.stat-change {
  display: flex;
  align-items: center;
  gap: var(--space-2);
  font-size: var(--font-size-sm);
  font-weight: var(--font-weight-medium);
}

.stat-change.positive {
  color: var(--success);
}

.stat-change.negative {
  color: var(--error);
}

/* Chart Card */
.chart-card {
  background: var(--glass-bg);
  border-radius: var(--radius-2xl);
  padding: var(--space-6);
  border: 1px solid var(--glass-border);
  backdrop-filter: blur(16px);
  -webkit-backdrop-filter: blur(16px);
  margin-bottom: var(--space-6);
  box-shadow: var(--shadow-sm);
  animation: fadeScale 0.6s cubic-bezier(0.4, 0, 0.2, 1) forwards;
  opacity: 0;
  transform: scale(0.95);
}

.chart-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: var(--space-6);
}

.chart-title {
  font-size: var(--font-size-xl);
  font-weight: var(--font-weight-semibold);
  color: var(--text-primary);
}

.chart-legend {
  display: flex;
  gap: var(--space-4);
}

.legend-item {
  display: flex;
  align-items: center;
  gap: var(--space-2);
}

.legend-color {
  width: 12px;
  height: 12px;
  border-radius: var(--radius-sm);
}

.legend-label {
  color: var(--text-secondary);
  font-size: var(--font-size-sm);
}

/* Activity Timeline */
.timeline {
  position: relative;
  padding-left: var(--space-6);
}

.timeline::before {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  width: 2px;
  background: var(--gradient-primary);
  opacity: 0.3;
}

.timeline-item {
  position: relative;
  padding-bottom: var(--space-6);
  transition: var(--transition-fast);
}

.timeline-item:hover {
  transform: translateX(5px);
}

.timeline-item::before {
  content: '';
  position: absolute;
  left: calc(var(--space-6) * -1);
  top: 0;
  width: 12px;
  height: 12px;
  border-radius: var(--radius-full);
  background: var(--gradient-primary);
  border: 2px solid var(--bg-primary);
}

.timeline-time {
  font-size: var(--font-size-sm);
  color: var(--text-secondary);
  margin-bottom: var(--space-2);
}

.timeline-content {
  color: var(--text-primary);
  line-height: var(--line-height-relaxed);
}

/* Loading Skeleton */
.loading-skeleton {
  animation: pulse 2s cubic-bezier(0.4, 0, 0.6, 1) infinite;
}

.skeleton-header {
  height: 60px;
  background: rgba(255, 255, 255, 0.1);
  border-radius: var(--radius-xl);
  margin-bottom: var(--space-6);
}

.skeleton-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: var(--space-6);
  margin-bottom: var(--space-6);
}

.skeleton-card {
  height: 180px;
  background: rgba(255, 255, 255, 0.1);
  border-radius: var(--radius-2xl);
}

.skeleton-content {
  height: 300px;
  background: rgba(255, 255, 255, 0.1);
  border-radius: var(--radius-2xl);
}

@keyframes pulse {
  0%, 100% { opacity: 1; }
  50% { opacity: 0.5; }
}

/* Dashboard Header */
.dashboard-header {
  margin-bottom: var(--space-8);
}

.header-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: var(--space-6);
  background: var(--glass-bg);
  border-radius: var(--radius-2xl);
  border: 1px solid var(--glass-border);
  backdrop-filter: blur(16px);
  -webkit-backdrop-filter: blur(16px);
  box-shadow: var(--shadow-sm);
}

.welcome-section h1 {
  font-size: var(--font-size-3xl);
  font-weight: var(--font-weight-bold);
  color: var(--text-primary);
  margin: 0 0 var(--space-2) 0;
  background: var(--gradient-primary);
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
  line-height: var(--line-height-tight);
}

.welcome-subtitle {
  font-size: var(--font-size-base);
  color: var(--text-secondary);
  margin: 0;
  font-weight: var(--font-weight-normal);
}

.header-actions {
  display: flex;
  align-items: center;
  gap: var(--space-3);
}

.notification-btn {
  position: relative;
  background: var(--bg-surface);
  border: 1px solid var(--border-medium);
  border-radius: var(--radius-lg);
  padding: var(--space-3);
  color: var(--text-primary);
  cursor: pointer;
  transition: var(--transition-fast);
  font-size: var(--font-size-lg);
}

.notification-btn:hover {
  background: var(--bg-elevated);
  transform: translateY(-1px);
  box-shadow: var(--shadow-sm);
}

.notification-badge {
  position: absolute;
  top: calc(var(--space-1) * -1);
  right: calc(var(--space-1) * -1);
  background: var(--error);
  color: var(--text-primary);
  border-radius: var(--radius-full);
  width: 20px;
  height: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: var(--font-size-xs);
  font-weight: var(--font-weight-bold);
}

.user-avatar {
  margin-left: var(--space-3);
}

.avatar-circle {
  width: 48px;
  height: 48px;
  border-radius: var(--radius-full);
  background: var(--gradient-primary);
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--text-primary);
  font-weight: var(--font-weight-bold);
  font-size: var(--font-size-lg);
  cursor: pointer;
  transition: var(--transition-fast);
  box-shadow: var(--shadow-sm);
}

.avatar-circle:hover {
  transform: scale(1.05);
  box-shadow: var(--shadow-md);
}

/* Quick Actions */
.quick-actions-section {
  margin-bottom: var(--space-8);
}

.section-title {
  font-size: var(--font-size-xl);
  font-weight: var(--font-weight-semibold);
  color: var(--text-primary);
  margin-bottom: var(--space-4);
}

.quick-actions-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: var(--space-4);
}

.quick-action-card {
  background: var(--glass-bg);
  border: 1px solid var(--glass-border);
  border-radius: var(--radius-xl);
  padding: var(--space-5);
  display: flex;
  align-items: center;
  gap: var(--space-4);
  cursor: pointer;
  transition: var(--transition-normal);
  position: relative;
  overflow: hidden;
  backdrop-filter: blur(12px);
  -webkit-backdrop-filter: blur(12px);
}

.quick-action-card::before {
  content: '';
  position: absolute;
  inset: 0;
  background: var(--action-color);
  opacity: 0;
  transition: opacity var(--transition-normal);
}

.quick-action-card:hover::before {
  opacity: 0.05;
}

.quick-action-card:hover {
  transform: translateY(-2px);
  box-shadow: var(--shadow-lg);
  border-color: var(--border-medium);
}

.action-icon {
  width: 48px;
  height: 48px;
  border-radius: var(--radius-lg);
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--text-primary);
  font-size: var(--font-size-xl);
  flex-shrink: 0;
  background: var(--action-color);
}

.action-content {
  flex: 1;
}

.action-title {
  font-size: var(--font-size-base);
  font-weight: var(--font-weight-semibold);
  color: var(--text-primary);
  margin: 0 0 var(--space-1) 0;
}

.action-description {
  font-size: var(--font-size-sm);
  color: var(--text-secondary);
  margin: 0;
  line-height: var(--line-height-snug);
}

.action-arrow {
  color: var(--text-tertiary);
  transition: var(--transition-fast);
  font-size: var(--font-size-lg);
}

.quick-action-card:hover .action-arrow {
  transform: rotate(90deg);
  color: var(--text-primary);
}

/* Content Grid */
.content-grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: var(--space-6);
  margin-bottom: var(--space-8);
}

/* Card Headers */
.card-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: var(--space-4);
  padding-bottom: var(--space-3);
  border-bottom: 1px solid var(--border-light);
}

.edit-btn, .view-all-btn {
  background: var(--gradient-primary);
  color: var(--text-primary);
  border: none;
  padding: var(--space-2) var(--space-4);
  border-radius: var(--radius-lg);
  font-size: var(--font-size-sm);
  font-weight: var(--font-weight-medium);
  cursor: pointer;
  display: flex;
  align-items: center;
  gap: var(--space-2);
  transition: var(--transition-fast);
  box-shadow: var(--shadow-xs);
}

.edit-btn:hover, .view-all-btn:hover {
  transform: translateY(-1px);
  box-shadow: var(--shadow-sm);
}

/* Activities */
.activities-list {
  display: flex;
  flex-direction: column;
  gap: var(--space-4);
}

.activity-item {
  display: flex;
  align-items: flex-start;
  gap: var(--space-4);
  padding: var(--space-4);
  background: rgba(255, 255, 255, 0.02);
  border-radius: var(--radius-lg);
  transition: var(--transition-fast);
  border: 1px solid var(--border-light);
}

.activity-item:hover {
  background: rgba(255, 255, 255, 0.05);
  transform: translateX(2px);
  border-color: var(--border-medium);
}

.activity-icon {
  width: 40px;
  height: 40px;
  border-radius: var(--radius-lg);
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: var(--font-size-lg);
  flex-shrink: 0;
}

.activity-icon.success {
  background: var(--success-bg);
  color: var(--success);
}

.activity-icon.info {
  background: var(--info-bg);
  color: var(--info);
}

.activity-icon.warning {
  background: var(--warning-bg);
  color: var(--warning);
}

.activity-content {
  flex: 1;
}

.activity-title {
  font-size: var(--font-size-base);
  font-weight: var(--font-weight-semibold);
  color: var(--text-primary);
  margin: 0 0 var(--space-1) 0;
}

.activity-description {
  font-size: var(--font-size-sm);
  color: var(--text-secondary);
  margin: 0 0 var(--space-2) 0;
  line-height: var(--line-height-relaxed);
}

.activity-time {
  font-size: var(--font-size-xs);
  color: var(--text-tertiary);
  display: flex;
  align-items: center;
  gap: var(--space-1);
}

/* Role Badge */
.role-badge {
  background: var(--gradient-primary);
  color: var(--text-primary);
  padding: var(--space-1) var(--space-3);
  border-radius: var(--radius-full);
  font-size: var(--font-size-xs);
  font-weight: var(--font-weight-medium);
  text-transform: capitalize;
  display: inline-flex;
  align-items: center;
}

/* Privileges Grid */
.privileges-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: var(--space-3);
}

.privilege-item {
  display: flex;
  align-items: center;
  gap: var(--space-3);
  padding: var(--space-4);
  background: rgba(255, 255, 255, 0.02);
  border-radius: var(--radius-lg);
  transition: var(--transition-fast);
  border: 1px solid var(--border-light);
}

.privilege-item:hover {
  background: rgba(255, 255, 255, 0.05);
  transform: translateY(-1px);
  border-color: var(--border-medium);
}

.privilege-icon {
  font-size: var(--font-size-xl);
  color: var(--text-tertiary);
  flex-shrink: 0;
  transition: var(--transition-fast);
}

.privilege-item:hover .privilege-icon {
  color: var(--text-primary);
}

/* Enhanced Animations */
@keyframes fadeInUp {
  0% {
    opacity: 0;
    transform: translateY(30px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes cardAppear {
  0% {
    transform: translateY(20px);
    opacity: 0;
  }
  100% {
    transform: translateY(0);
    opacity: 1;
  }
}

@keyframes fadeScale {
  0% {
    opacity: 0;
    transform: scale(0.95);
  }
  100% {
    opacity: 1;
    transform: scale(1);
  }
}

@keyframes gradientShift {
  0% { background-position: 0% 50%; }
  50% { background-position: 100% 50%; }
  100% { background-position: 0% 50%; }
}

/* Animation Delays */
.stat-card:nth-child(1) { animation-delay: 0.1s; }
.stat-card:nth-child(2) { animation-delay: 0.2s; }
.stat-card:nth-child(3) { animation-delay: 0.3s; }
.stat-card:nth-child(4) { animation-delay: 0.4s; }

.chart-card:nth-child(1) { animation-delay: 0.5s; }
.chart-card:nth-child(2) { animation-delay: 0.6s; }
.chart-card:nth-child(3) { animation-delay: 0.7s; }

/* Responsive Design */
@media (max-width: 1024px) {
  .content-grid {
    grid-template-columns: 1fr;
  }

  .header-content {
    flex-direction: column;
    gap: var(--space-4);
    text-align: center;
  }

  .welcome-section h1 {
    font-size: var(--font-size-2xl);
  }
}

@media (max-width: 768px) {
  .dashboard-container {
    padding: var(--space-4) var(--space-3);
  }

  .header-content {
    padding: var(--space-4);
  }

  .welcome-section h1 {
    font-size: var(--font-size-xl);
  }

  .quick-actions-grid {
    grid-template-columns: 1fr;
  }

  .dashboard-grid {
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    gap: var(--space-4);
  }

  .content-grid {
    grid-template-columns: 1fr;
  }

  .stat-card {
    padding: var(--space-4);
  }

  .stat-value {
    font-size: var(--font-size-2xl);
  }
}

@media (max-width: 480px) {
  .dashboard-container {
    padding: var(--space-3) var(--space-2);
  }

  .header-content {
    padding: var(--space-3);
  }

  .welcome-section h1 {
    font-size: var(--font-size-lg);
  }

  .stat-card {
    padding: var(--space-3);
  }

  .stat-value {
    font-size: var(--font-size-xl);
  }

  .quick-action-card {
    padding: var(--space-4);
    flex-direction: column;
    text-align: center;
    gap: var(--space-3);
  }

  .action-icon {
    align-self: center;
  }
}